// scroll
const scrollSpy = () => {
	const menuItems = document.querySelectorAll(".navbar__link")
	const scrollSpySections = document.querySelectorAll("section")

	const handleScrollSpy = () => {
		if (document.body.classList.contains("main-content")) {
			const header = document.querySelector("header")
			const headerHeight = header.offsetHeight / 1.8
			const sections = []

			if (window.scrollY > headerHeight) {
				scrollSpySections.forEach((section) => {
					if (
						window.scrollY <=
						section.offsetTop + section.offsetHeight - 500
					) {
						sections.push(section)

						const activeSection = document.querySelector(
							`[href*="${sections[0].id}"]`
						)

						menuItems.forEach((item) => item.classList.remove("active2"))

						activeSection.classList.add("active2")
					}
				})
			} else {
				menuItems.forEach((item) => item.classList.remove("active2"))
			}
		}
	}

	window.addEventListener("scroll", handleScrollSpy)
}

// section animations
const sectionAnimations = () => {
	const sections = document.querySelectorAll("section")
	function getRootMargin() {
		const isMobile = window.innerWidth < 768
		return isMobile ? "0.001" : "0.1"
	}
	const options = {
		root: null,
		threshold: getRootMargin(),
	}

	const observer = new IntersectionObserver((entries, observer) => {
		entries.forEach((entry) => {
			if (entry.isIntersecting) {
				entry.target.classList.add("section-active")
				observer.unobserve(entry.target)
			}
		})
	}, options)

	sections.forEach((section) => {
		observer.observe(section)
	})
}

// accordion
const accordion = () => {
	const accordion = document.querySelector(".accordion")
	const accordionBtns = document.querySelectorAll(".accordion__box-btn, .plus")

	function openAccordionItems() {
		if (this.nextElementSibling && this.nextElementSibling.classList) {
			const isActive = this.nextElementSibling.classList.contains("active")
			closeAccordionItem()
			if (!isActive) {
				this.nextElementSibling.classList.add("active")
			}
		}
	}

	const closeAccordionItem = () => {
		const allActiveItems = document.querySelectorAll(".accordion__info")
		allActiveItems.forEach((item) => item.classList.remove("active"))
	}

	const clickOutsideAccordion = (e) => {
		if (
			e.target.classList.contains("accordion__box-btn") ||
			e.target.classList.contains("accordion__info") ||
			e.target.classList.contains("accordion__info-text") ||
			e.target.classList.contains("plus")
		)
			return

		closeAccordionItem()
	}

	accordionBtns.forEach((btn) =>
		btn.addEventListener("click", openAccordionItems)
	)

	window.addEventListener("click", clickOutsideAccordion)
}
// arrow to top
const arrowToTop = () => {
	document.addEventListener("DOMContentLoaded", function () {
		const scrollToTopBtn = document.querySelector(".arrow-to-top")

		window.addEventListener("scroll", function () {
			// Pokaż strzałkę, gdy użytkownik przewinie stronę o pewną ilość pikseli
			if (window.scrollY > 300) {
				scrollToTopBtn.classList.add("arrow-to-top-active")
				// scrollToTopBtn.classList.remove("arrow-to-top-inactive")
			} else {
				scrollToTopBtn.classList.remove("arrow-to-top-active")
				// scrollToTopBtn.classList.add("arrow-to-top-inactive")
			}
		})
	})
}

// footer year auto change
const footerYear = () => {
	const footerNumber = document.querySelector("#footer-number")
	let date = new Date()
	let footerYear = date.getFullYear()
	footerNumber.textContent = footerYear
}

// burger menu
const burgerMenu = () => {
	const burger = document.querySelector(".burger")
	const toggleMenu = () => {
		const menu = document.querySelector(".navbar__menu")
		const body = document.querySelector("body")
		menu.classList.toggle("burger-active")
		body.classList.toggle("no-scroll")
	}

	const navbarItems = document.querySelectorAll(".navbar__menu-item")
	navbarItems.forEach((link) => {
		link.addEventListener("click", function () {
			const menu = document.querySelector(".navbar__menu")
			if (menu.classList.contains("burger-active")) {
				menu.classList.remove("burger-active")
				document.querySelector("body").classList.remove("no-scroll")
			}
		})
	})

	burger.addEventListener("click", toggleMenu)
}

const setup = () => {
	burgerMenu()
	scrollSpy()
	sectionAnimations()
	accordion()
	arrowToTop()
	footerYear()
}
setup()
